<template>
  <div class="login">
    <div>
      <van-nav-bar title="账号登录" left-arrow @click-left="onClickLeft" fixed placeholder>
        <template #left>
          <van-icon name="cross" size="18" color="#2c2c2c" />
        </template>
      </van-nav-bar>
    </div>
    <div class="logo-img">
      <div>
        <img v-show="isImport" class="letf" src="../assets/22_open.4ea5f239.png" alt />
        <img v-show="!isImport" class="letf" src="../assets/22_close.9382a5a8.png" alt />
      </div>
      <div class="middel-img">
        <img class="letf" src="../assets/bilibili-fill.png" alt />
        <img class="letf" src="../assets/bilibili.png" alt />
      </div>
      <div>
        <img v-show="isImport" class="right" src="../assets/33_open.f7d7f655.png" alt />
        <img v-show="!isImport" class="right" src="../assets/33_close.a8c18fc8.png" alt />
      </div>
    </div>
    <!-- 登录表单 -->
    <div class="login-box">
      <van-form @submit="login" :show-error="false">
        <van-field
          v-model="userInfo.phone"
          name="账号"
          placeholder="请输入bilibili账号"
          :rules="[{ required: true, message: '请填写手机号' }, {pattern: /^1[3-9]\d{9}$/, message: '账号格式不正确'}]"
        />
        <van-field
          v-model="userInfo.password"
          name="密码"
          :type="loginPasswordStatus ? 'text' : 'password'"
          :right-icon="loginPasswordStatus ? 'eye-o' : 'closed-eye'"
          placeholder="请输入密码"
          :rules="[{ required: true, message: '请填写密码' }, {pattern: /^[A-Za-z]\w{5,15}$/, message: '支持数字字母下划线组合且须以字母开头'}]"
          @focus="focus"
          @blur="blur"
          @click-right-icon="setPasswordStatus('loginPasswordStatus')"
        />
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">登录</van-button>
          <van-button @click="showRegister" round block type="info" native-type="button">立即注册</van-button>
        </div>
      </van-form>
    </div>

    <!-- 注册表单 -->
    <div class="reginster-box">
      <van-action-sheet v-model="show" title="立即注册">
        <div class="content">
          <div class="reginster-box">
            <van-form @submit="register" :show-error="false">
              <van-field
                v-model="user.phone"
                name="常用手机号"
                placeholder="请输入常用手机号"
                :rules="[{ required: true, message: '请填写手机号' }, {pattern: /^1[3-9]\d{9}$/, message: '手机号格式不正确'}]"
              />
              <van-field
                v-model="user.password"
                :type="registerPasswordStatus ? 'text' : 'password'"
                name="密码"
                placeholder="请输入密码"
                :right-icon="registerPasswordStatus ? 'eye-o' : 'closed-eye'"
                :rules="[{ required: true, message: '请填写密码' }, {pattern: /^[A-Za-z]\w{5,15}$/, message: '支持数字字母下划线组合且须以字母开头'}]"
                @focus="focus"
                @blur="blur"
                @click-right-icon="setPasswordStatus('registerPasswordStatus')"
              />
              <van-field
                v-model="user.nickName"
                type="text"
                name="昵称"
                placeholder="昵称(1-10位)"
                :rules="[{ required: true, message: '请填写昵称' }, {pattern: /[A-Za-z\u4e00-\u9fa5]{1,10}/, message: '支持中英文组合'}]"
              />
              <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">登录/登录</van-button>
              </div>
            </van-form>
          </div>
        </div>
      </van-action-sheet>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      //用户登录数据
      userInfo: {
        phone: "",
        password: ""
      },
      //用户注册数据
      user: {
        phone: "",
        password: "",
        nickName: ""
      },
      show: false,
      // 是否显示密码
      loginPasswordStatus: false,
      registerPasswordStatus: false,
      // 输入框是否聚焦
      isImport: true
    };
  },
  created() {},
  methods: {
        //登录
    login() {
      this.axios({
        method: "post",
        url: "http://www.kangliuyong.com:10002/login",
        data: {
          ...this.userInfo
        }
      })
        .then(res => {
          if (res.data.code === 200) {
             this.$notify({
              type: "success",
              message: res.data.msg,
              duration: 2000
            });
            //将token保存在本地存储中
            localStorage.setItem('token', res.data.token);
            setTimeout(() => {
              this.$router.push({name: 'Home'});
            }, 2000)
          } else {
            this.$notify({
              type: "danger",
              message: res.data.msg,
              duration: 2000
            });
          }
        })
        .catch(err => {
          
          this.$notify({
            type: "danger",
            message: '登录失败',
            duration: 2000
          });
        });
    },
    //  注册
    register() {
      this.axios({
        method: "post",
        url: "http://www.kangliuyong.com:10002/register",
        data: {
          ...this.user
        }
      })
        .then(res => {
         
          if (res.data.code === 100) {
            this.$notify({
              type: "success",
              message: res.data.msg,
              duration: 2000
            });

            this.show = false;

            for (let key in this.user) {
              this.user[key] = "";
            }
          } else {
            this.$notify({
              type: "danger",
              message: res.data.msg,
              duration: 2000
            });
          }
        })
        .catch(err => {
          this.$notify({
            type: "danger",
            message: "注册失败",
            duration: 2000
          });
        });
    },

    showRegister() {
      this.show = true;
    },
    //切换登录密码状态
    setPasswordStatus(key) {
      this[key] = !this[key];
    },
    //切换登录密码状态
    setPasswordStatus(key) {
      this[key] = !this[key];
    },
    // 返回上一级
    onClickLeft() {
      this.$router.push({
        name: "My"
      });
    },
    //  密码输入栏聚焦事件
    focus() {
      this.isImport = false;
    },
    //  密码输入栏失去聚焦事件
    blur() {
      this.isImport = true;
    }
  }
};
</script>

<style lang="scss" scoped>
.login {
  .logo-img {
    width: 100%;
    display: flex;
    justify-content: space-between;
    img {
      width: 100%;
    }
    .middel-img {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 35%;
      }
    }
  }
  .login-box {
    padding: 10px 20px;
    button {
      margin-top: 10px;
      &:nth-child(2) {
        background-color: white;
        color: #1989fa;
      }
    }
  }
}
</style>